@import url(//fonts.googleapis.com/css?family=Chivo&display=swap);
@import url(https://fonts.googleapis.com/css?family=Chivo:700&display=swap);
.scrollContainer{overflow-y:auto;height:96%;width:98%;margin-top:10px;margin-left:13px;margin-right:5px}.scrollContainer::-webkit-scrollbar{width:8px}.scrollContainer::-webkit-scrollbar-thumb{background:#353535;border-radius:16px}.scrollContainer::-webkit-scrollbar-thumb:hover{background:#dadada}

.imprintOuterBox{position:absolute;width:90%;height:90%;left:5%;top:5%;background-color:#464646;display:flex;flex-flow:row wrap}.imprintHeadline{order:1;flex:100% 1;text-align:center;font-family:"Chivo",serif;color:#ffff;margin-top:40px;font-size:40px}.imprintTextbox{order:2;flex:100% 1;font-family:"Chivo", serif;color:#ffff;font-size:20px;position:absolute;top:130px;left:5%;width:90%;height:70%}@media only screen and (max-width: 600px){.imprintOuterBox{position:fixed;width:100%;height:auto;left:0px;top:60px;bottom:90px;background-color:#464646;display:flex;flex-flow:row wrap}.imprintHeadline{order:1;flex:100% 1;text-align:center;font-family:"Chivo",serif;color:#ffff;margin-top:20px;font-size:30px}.imprintTextbox{order:2;flex:100% 1;font-family:"Chivo", serif;color:#ffff;font-size:6vw;position:absolute;top:80px;left:2%;width:94%;height:58vh}}@media only screen and (max-width: 400px) and (max-height: 700px){.imprintTextbox{height:56vh}}

